	module.exports = {"data":{"wordpressPost":{"title":"Charlotte Tilbury Reveals The Surprising Secret To Perfect Eyeliner","content":"<p>Makeup artist <strong>Charlotte Tilbury</strong> knows how to use eyeliner to create a sexy, strong, feminine look.</p>\n<p>Given that, it&#8217;s not surprising designer <strong>Donatella Versace</strong> tapped her for the Versus by Versace runway show at London Fashion Week 2017. Word is Donatella asked Charlotte for a strong eye and left the rest up to her. Charlotte responded by whipping up the most epic, rich black eyeliner we&#8217;ve seen in some time</p>\n<p>Donatella was dead impressed &#8211; and rightly so.</p>\n<p>&#8220;I asked Charlotte for a strong eye and she gave me the perfect graphic, punk eye of the strong Versus girl,&#8221; said Donatella.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3157\" style=\"width: 200px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3157\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213135/CT-Versus-004-200x300.jpg\" alt=\"\" width=\"200\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213135/CT-Versus-004-200x300.jpg 200w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213135/CT-Versus-004-768x1152.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213135/CT-Versus-004-683x1024.jpg 683w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213135/CT-Versus-004.jpg 1000w\" sizes=\"(max-width: 200px) 100vw, 200px\" /><figcaption class=\"wp-caption-text\">Strong, rich, black eyeliner that says it all.</figcaption></figure>\n<div class=\"page\" title=\"Page 1\">\n<div class=\"layoutArea\">\n<div class=\"column\">\n<p>And you won&#8217;t believe what Charlotte used to make the look so incredibly flattering, sultry, yet sophisticated all at the same time. <strong>Vaseline &#8230; yep, good old-fashion Vaso.</strong></p>\n<p>Charlotte first created a lived-in feline flick eye look using her very own Rock &#8216;n&#8217; Kohl liquid eyeliner pencil in Bedroom Black along the waterline. Then, she smudged it with a dab of <strong>Vaseline</strong>.</p>\n<p>So there you have it. The professional makeup artist&#8217;s secret to the perfect rock chick flick.</p>\n<p>Just in case you fancy recreating the look and heck &#8211; we reckon you should &#8211; this is what else she did next.</p>\n<p>Added a thick straight feline flick along the top lash line, also using Bedroom Black as well as The Feline Flick Liquid Eye Pen in Panther and then had models squeezed their eyes tight to smudge.</p>\n<figure id=\"attachment_3156\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3156\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213050/CT-Versus-001-300x200.jpg\" alt=\"\" width=\"300\" height=\"200\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213050/CT-Versus-001-300x200.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213050/CT-Versus-001-768x512.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213050/CT-Versus-001-1024x683.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Charlotte Tilbury &#8211; creating magic for Versus by Versace.</figcaption></figure>\n<p>“This look was inspired by the great and glorious Amy Winehouse’ iconic punky, graphic, overdrawn Feline Flick, as well as Kate Moss’ signature eye,&#8221; says Charlotte.</p>\n<p>And we couldn&#8217;t love it more.</p>\n<p>Charlotte added fat, full lashes and also a touch of a nude shade on the lips for full, sexy pouts. She recommends her own lipstick in Nude Kate or Hepburn Honey for those with darker skin tones.</p>\n</div>\n</div>\n</div>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/19213003/CT-Versus-006.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"bae4007a-5386-5cca-908e-6834ebef2d45"}}

